.header {
  position: relative;
  height: 100vh;

  &__logo {
    padding: 15px;
    background-color: $color-bg;
    color: $color-white;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);

    img {
      width: 30px;
      height: 30px;
      display: block; } }

  &__wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%; }

  &__title {
    font-family: 'Roboto', sans-serif;
    font-size: 44px;
    color: $color-title;
    font-weight: 400;
    text-align: center;

    @include r($sm) {
      font-size: 32px; } }

  &__btn {
    margin: 30px auto 0; } }
