.section-portfolio {
  padding-bottom: 200px;

  @include r($sm) {
    padding-bottom: 95px; }

  &__wrap {
    margin-top: 50px;
    padding-top: 20px;
    position: relative;

    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 5px;
      height: 100%;
      background-color: $color-white;
      top: 0;
      left: 50%;
      transform: translateX(-50%);

      @include r($sm) {
        right: 7px;
        left: auto; } } } }
