.menu {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    margin: 0 5px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    color: $color-dark;
    padding: 15px 20px;

    &:nth-child(2) {
      margin-right: 30px; }

    @include r($sm) {
      display: none; } }

  &__link {
    padding: 20px;
    colors: initial;
    text-decoration: none; }

  @include r($sm) {
    position: absolute;
    right: 20px;
    height: 100vh;
    width: 200px;
    justify-content: flex-start;
    align-items: flex-end;
    flex-direction: column;
    top: 50px;
    opacity: 0;
    z-index: -1;

    &.is-open {
      opacity: 1;
      transform: translateX(80vw) translateY(-10vh);
      z-index: 1; } } }
