.btn-custom {
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: $color-black;
  font-weight: 400;
  padding: 15px 20px;
  border: 1px solid $color-black;
  width: 180px;
  text-align: center;
  cursor: pointer;

  @include r($sm) {
    padding: 10px 15px;
    margin: 20px auto 0; } }

