.portfolio {
  position: relative;
  $this: &;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: calc(50% - 12px);
    width: 24px;
    height: 24px;
    border: 5px solid $color-white;
    top: 5px;
    border-radius: 50%;
    background-color: $color-bg;

    @include r($sm) {
      right: 0;
      left: auto; } }

  &__name {
    width: 40%;
    margin-bottom: 20px;

    @include r($sm) {
      width: 100%;
      padding-right: 40px; } }

  &__title {
    font-family: 'Helvetica', serif;
    font-size: 26px;
    color: $color-white;
    font-weight: 400;
    text-align: right;

    @include r($sm) {
      font-size: 20px; } }

  &__content {
    padding: 0 40px;
    width: 50%;
    margin-left: auto;

    @include r($sm) {
      margin-left: 0;
      width: 100%; } }

  &__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 20px;

    @include r($sm) {
      grid-template-columns: repeat(1, 1fr); } }

  &__link {
    position: relative;
    display: block;

    @include r($sm) {
      max-height: 165px; }

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba($color-bg, .5);
      transform: scale(0);
      transition: transform .3s ease-in-out; }

    &:last-of-type {
      grid-column-start: 1;
      grid-column-end: 3;

      @include r($sm) {
        grid-column-end: 2; } }

    &:hover {
      &::before {
        transform: scale(1); }

      #{$this}__icon {
        opacity: 1; } } }

  &__img {
    max-width: 100%;
    height: 100%;
    object-fit: cover; }

  &__icon {
    position: absolute;
    @include vcenter;
    opacity: 0;
    transition: opacity .3s ease-in-out; }

  &__download {
    font-family: 'Roboto', serif;
    text-decoration: none;
    font-size: 16px;
    line-height: 20px;
    position: relative;
    color: $color-paragraph;
    transition: all 0.6s;

    &:before {
      content: "";
      width: 0;
      height: 2px;
      position: absolute;
      bottom: -2px;
      left: 50%;
      background: $color-paragraph;
      transition: all 0.3s; }

    &:hover {
      color: $color-white;

      &:before {
        width: 100%;
        left: 0;
        background: $color-white; } } } }
